import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.scss']
})
export class CustomerComponent { 
  constructor(
    private router: Router, 
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent
  ) {
    
  } 
  gotoCustomerList(){
    if (this.vhAuth.checkMyPermission("management_enable_customer")) this.router.navigate(['/sales/dashboard/manage/partner/customer/customer-list'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
    
  }

  gotoCustomerGroup(){
    if (this.vhAuth.checkMyPermission("management_enable_customer_group")) this.router.navigate(['/sales/dashboard/manage/partner/customer/customer-group'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
    
  }

  goBack(){
    this.router.navigate(['/sales/dashboard/manage/partner'])
  }
}
